<template>
  <div id="FeedbackView" v-if="show.snapshot">
    <div class="center-wrapper">
    <div class="center">
    <video id="video" class="camon" autoplay></video>
    <canvas id="screenshot" class="camoff"></canvas>
    </div>
    </div>
    <div class="button-div camon single">
      <a-button type="primary" @click="checkPhoto">
        <i class="fas fa-check"></i>
      </a-button>
    </div>


    
    <div>
      <!-- {{ debugMsg  }} -->
    </div>
    <a-row>
    <a-col :span="12">
    <div class="button-div not-exist double camoff">
      <a-button type="primary" @click="getImage">
        <i class="fas fa-undo-alt"></i>
        <!-- <a-icon type="undo" /> -->
        <span> &nbsp; Retake</span>
      </a-button>
    </div>
    </a-col>
        <a-col :span="12">
    <div class="button-div not-exist camoff double">
      <a-button type="primary" @click="getFeedback">
        <span>Looking Good!&nbsp;</span>
           <i class="far fa-smile-wink"></i>
           <!-- <a-icon type="smile" /> -->
      </a-button>
    </div>
    </a-col>
    </a-row>
  </div>
  <Loading v-if="show.load"/>
  <Score v-if="show.feedback" :data="result"/>

</template>
<script lang="js">
import Score from '@/components/Score.vue';
import Loading from '@/components/Loading.vue';
import Button from 'ant-design-vue/lib/button';
import Row from 'ant-design-vue/lib/row';
import Col from 'ant-design-vue/lib/col';
import axios from 'axios';
import * as d3 from 'd3';
import { turnOnCam, transform, isMobile } from '@/assets/js/web';
// import { plotFeedback } from '@/assets/js/plot'
import { feedbackData } from '@/assets/js/constant'
import { mapGetters } from 'vuex';
const url = 'https://atie.leoyuholo.com/predict_foodwaste' //'https://localhost:8867/predict_foodwaste'
//const tmp  = 'https://atie.leoyuholo.com/predict_foodwaste'//'localhost:12072/assess'
export default {
  name: 'Feedback',
  components: {
    [Button.name]: Button,
    [Row.name]: Row,
    [Col.name]: Col,
    Score,
    Loading
  },
  data() {
    return {
      stream: null,
      video: null,
      canvas: null,
      show: {
        load: false,
        snapshot: true,
        feedback: false
      },
      result: feedbackData,
      debugMsg: ''
    }
  },
  computed: {
    ...mapGetters(['dev'])
  },
  created() {
  },
  mounted() {
    this.initSize()
    this.getImage()
  },
  methods: {
    initSize() {
      this.canvas = document.getElementById('screenshot')
      this.video = document.getElementById('video')
    },
    display(name) {
      this.show.load = name == 'load' ? true : false
      this.show.snapshot = name == 'snapshot' ? true : false
      this.show.feedback = name == 'feedback' ? true : false
    },
    getImage() {
      let check = isMobile()
      if(!check) {
        alert('Your device does not support uploading photos :-(')
        window.location = '/'
        return
      }
      let self = this
      console.log('get image now')
      turnOnCam().then(function(stream) {
        d3.selectAll('.camon').classed('not-exist', false)
        d3.selectAll('.camoff').classed('not-exist', true)
        self.stream = stream
        self.debug()
        self.video.src = URL.createObjectURL(stream)
      }).catch(function() {
        self.video.srcObject = self.stream
      })
    },
    debug() {
      window.stream = this.stream
      window.canvas = this.canvas
      window.video = this.video
    },
    checkPhoto() {
      if(!this.stream) return
      let width = this.video.offsetWidth
      let height = this.video.offsetHeight
      this.canvas.height = height
      this.canvas.width = width
      let pixelHeight = this.stream.getVideoTracks()[0].getSettings().height
      let pixelWidth = this.stream.getVideoTracks()[0].getSettings().width
      this.canvas.getContext("2d").drawImage(this.video, 0, 0, pixelWidth, pixelHeight, 0, 0, this.canvas.width, this.canvas.height)
      this.debugMsg = `video:(${this.video.width} ${this.video.height})  canvas: (${width} ${height})  real: (${pixelWidth}, ${pixelHeight})`
      this.stream.getTracks().forEach(function(track) { track.stop() })
      this.video.src = null
      this.video.srcObject = null
      d3.selectAll('.camon').classed('not-exist', true)
      d3.selectAll('.camoff').classed('not-exist', false)
    },
    getFeedback() {
      let self = this
      this.display('load')
      if(this.dev) {
        d3.json('./dataset/mock-recognition.json').then((data) => {
          data.score = Math.random() > 0.33? 90: (Math.random() > 0.5? 70 : 20)
          setTimeout(() => {
            self.result = transform(data)
            self.$store.dispatch('updateAction')
            self.display('feedback')
          }, 2000)
         
          // console.log('plot on feedback')
          // plotFeedback(data.waste, 'food-lolipop')
        })
        return
      }
      // drawImage(this.canvas,  './figures/test.jpg')   // for test on a given file
      this.canvas.toBlob( function(blob) {
          axios.post(url, blob, {
          headers: {
            'Access-Control-Allow-Origin': 'true',
            'Content-Type': blob.type
          }
        }).then((d) => {
          // let debug = true
          // if (debug || !('waste' in d.data) && parseInt(d.data.score) != 100) {
          //   alert('Oops, try again.')
          //   self.display('snapshot')
          //   return
          // }
          self.result = transform(d.data)
          self.$store.dispatch('updateAction')
          self.display('feedback')
        })
      }, "image/jpeg", 0.95)
    }
  }
}
</script>

<style lang="scss">
.not-exist {
  display: none;
}
</style>
