<template>
  <div class="score-feedback">
    <img :src="`./figures/${imgSrc[level]}`" />
    <div class="relative-wrapper">
      <div class="details">
        <div :class="`score-number circle level_${level}`">
          <div class="center score-text"><div>{{ data.score }}</div></div>
        </div>
        <div class="score-comment">
          {{ sentence[level] }}
        </div>
      </div>
      <div class="comment-box" @click="sendFeedback()">
        <i class="icon far fa-comment-dots"></i>
        <span><i>feedback</i></span>
      </div>
    </div>
      <div class="board card">
        <div class="wrapper">
         {{ data.food ? 'Indentified food:' + data.food : 'Empty Plate!' }}
        </div>
      </div>
      <div class="food-distribution board">
        <div id="food-lolipop">
        </div>
      </div>
    <a-row>
    <a-col :span="12">
    <div :class="`button-div camoff level_${level}`">
    <router-link to="/">
      <a-button size="large">
        <i class="fas fa-home"></i>
        <span> &nbsp; HKUST Today</span>
      </a-button>
      </router-link>
    </div>
    </a-col>
        <a-col :span="12">
    <div :class="`button-div camoff level_${level}`">
    <router-link to="/profile">
      <a-button size="large">
        <span>Personal Profile&nbsp;</span>
        <i class="fas fa-user-circle"></i>
      </a-button>
      </router-link>
    </div>
    </a-col>
    </a-row>
  </div>
</template>

<script lang="js">
import Row from 'ant-design-vue/lib/row';
import Col from 'ant-design-vue/lib/col';
import Button from 'ant-design-vue/lib/button';
import { plotFeedback } from '@/assets/js/plot'
export default {
  name: 'Score',
  props: ['data'],
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Button.name]: Button
  },
  data() {
    return {
      imgSrc: ['balloon.svg', 'shout.svg', 'bin.svg'],
      sentence: [
        'Good Job! You have cleaned up your plate.',
        'Try harder! You can do better next time!',
        'Come on! What’s wrong with today’s dish?'
      ]
    }
  },
  computed: {
    level() {
      if (this.data.score >= 85) return 0
      if (this.data.score >= 60) return 1
      return 2
    }
  },
  watch: {
  },
  mounted() {
    console.log('plot on score')
    plotFeedback(this.data.waste, 'food-lolipop')
  },
  methods: {
    sendFeedback() {
      console.log('click feedback')
    }
  }
}
</script>
<style lang="scss"></style>
